.side {
    .none-data {
        height: 100%;
        p {
            font-weight: 500;
            font-size: 18px;
            line-height: 28px;
            color: color(grey, 900);

            &:before {
                content: '';
                width: 72px;
                height: 72px;
                margin: 0 auto;
                display: block;
                background: url('../images/ic-exclamation_large_off.png') no-repeat;
            }
        }
    }
    &__head {
        padding: 40px 40px 0;

        + .side__inner {
            margin-top: 10px;
            padding: 0px 40px 0 40px;
            overflow-y: auto;
            height: calc(100% - 79px);
        }
    }
    &__inner {
        padding: 40px 40px 0 40px;
        height: 100%;
        overflow-y: auto;

        &:after {
            content: '';
            display: block;
            height: 40px;
        }
    }
    &__wrap {
        position: fixed;
        top: 80px;
        right: 0;
        z-index: 100;
        height: calc(100% - 79px);

        width: 461px;
        background-color: $white;
        border-left: 1px solid color(grey, 200);

        &.is-deactive {
            transform: translateX(100%);
            transition: transform 0.6s cubic-bezier(0.22, 0.86, 0.62, 1.06);
            z-index: $zindex-sidebar-deactive;
        }

        &.is-active {
            transform: translateX(0);
            transition: transform 0.6s cubic-bezier(0.22, 0.86, 0.62, 1.06);
            transition-delay: 400ms;
            z-index: $zindex-sidebar-active;
        }

        .swiper-wrapper {
            height: auto;
        }

        .swiper-slide {
            height: auto;
        }
    }

    &__frame__html {
        overflow: hidden;
    }

    &__frame__wrap {
        background-color: color(blue, 800);
        border-radius: 8px;

        padding: 40px 24px;
        color: $white;
    }
}
